
a {
  text-align: center;
  position: absolute;
  top: 950px;
  left: 350px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
button {
  border: none;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 2px;
  font-size: 3em;
}
button:hover {
  cursor: pointer;
}
button:focus {
  outline: none;
}
button:active {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}
.but-0 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  position: absolute;
  left: 0px;
  top: 0px;
  color: #A8A429;
}
.but-0.active {
  color: #FFF700 !important;
}
.but-0:active {
  color: #FFF700;
}
.but-1 {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  position: absolute;
  left: 500px;
  top: 0px;
  color: #B23D2B;
}
.but-1:active {
  color: #E81F00;
}
.but-1.active {
  color: #E81F00 !important;
}
.but-2 {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  position: absolute;
  left: 0px;
  top: 500px;
  color: #222B8C;
}
.but-2:active {
  color: #0015FF;
}
.but-2.active {
  color: #0015FF !important;
}
.but-3 {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  position: absolute;
  left: 500px;
  top: 500px;
  color: #1C9420;
}
.but-3:active {
  color: #0CE813;
}
.but-3.active {
  color: #0CE813 !important;
}
.fa-icon {
  width: auto;
  height: 420px;
}
#game {
  width: 900px;
  text-align: center;
  height: 900px;
  position: relative;
}
h1 {
  text-align: center;
  position: absolute;
  font-size: 6em;
  top: 900px;
  left: 150px;
}
.icon:hover {
  cursor: pointer;
}
.level {
  background-color: white;
  font-size: 6em;
}
#middle-row {
  border-width: medium;
  border-style: solid;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  position: absolute;
  left: 280px;
  top: 300px;
}
.strictButton {
  background-color: #0CE813;
}
.strictButton.strict {
  background-color: #E81F00;
}
#top-row {
  font-size: 140px;
}




